<template>
  <div>
    <div class="header pb-8 pt-5 pt-lg-8 d-flex align-items-center profile-header"
        style="min-height: 600px; background-image: url(img/theme/img-1-1000x600.jpg); background-size: cover; background-position: center top;">
      <b-container fluid>
        <!-- Mask -->
        <span class="mask bg-gradient-success opacity-8"></span>
        <!-- Header container -->
        <b-container fluid class="d-flex align-items-center">
          <b-row >
            <b-col lg="7" md="10">
              <h1 class="display-2 text-white">{{$t('lang.sloganTitle')}}</h1>
              <p class="text-white mt-0 mb-5">{{$t('lang.sloganMore')}}</p>
              <a href="/#/createToken" class="btn btn-info">{{$t('lang.toCreate')}}</a>
              <a href="javascript:void(0)" class="btn btn-info" @click="initOwner" v-if="account == admin">Init Owner</a>
              <a href="javascript:void(0)" class="btn btn-info" @click="withdraw" v-if="account == admin">Withdraw</a>
            </b-col>
          </b-row>
        </b-container>
      </b-container>
    </div>

    <b-container fluid class="mt--6">
      <b-row>
        <b-col xl="3" md="6">
          <stats-card :title="$t('lang.homeTxt1')"
                      type="gradient-red"
                      sub-title="$95.21B"
                      icon="ni ni-money-coins"
                      class="mb-4">

            <template slot="footer">
              <span class="text-success mr-2">3.48%</span>
              <span class="text-nowrap">{{$t('lang.homeTxt')}}</span>
            </template>
          </stats-card>
        </b-col>
        <b-col xl="3" md="6">
          <stats-card :title="$t('lang.homeTxt2')"
                      type="gradient-orange"
                      sub-title="10.32M"
                      icon="ni ni-chart-bar-32"
                      class="mb-4">

            <template slot="footer">
              <span class="text-success mr-2">12.18%</span>
              <span class="text-nowrap">{{$t('lang.homeTxt')}}</span>
            </template>
          </stats-card>
        </b-col>
        <b-col xl="3" md="6">
          <stats-card :title="$t('lang.homeTxt3')"
                      type="gradient-green"
                      sub-title="2,004,542"
                      icon="ni ni-active-40"
                      class="mb-4">

            <template slot="footer">
              <!-- <span class="text-danger mr-2">5.72%</span> -->
              <span class="text-success mr-2">5.72%</span>
              <span class="text-nowrap">{{$t('lang.homeTxt')}}</span>
            </template>
          </stats-card>

        </b-col>
        <b-col xl="3" md="6">
          <stats-card :title="$t('lang.homeTxt4')"
                      type="gradient-info"
                      sub-title="4,965"
                      icon="ni ni-chart-pie-35"
                      class="mb-4">

            <template slot="footer">
              <span class="text-success mr-2">54.8%</span>
              <span class="text-nowrap">{{$t('lang.homeTxt')}}</span>
            </template>
          </stats-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  import EditProfileForm from './UserProfile/EditProfileForm.vue';
  import UserCard from './UserProfile/UserCard.vue';

  import { useERC20V3FactoryContract } from '@/utils/useContract';
  import { mapState } from 'vuex';
  import { formatAmount, parseAmount, accMul, accDiv, toFixed, accAdd, accSub, accGt, significantDigits, gasProcessing } from '@/utils/format.js';

  export default {
    components: {
      EditProfileForm,
      UserCard
    },
    data() {
      return {
        // admin: '0x32f4A924d6815b9b50864CAeD3bc9782BEa48662',
        admin: '0x3F5695e87BC0F86276B407fdB5E971b4E68c5ed9',
      };
    },
    computed: {
      ...mapState(['account', 'provider', 'gasPrice', 'slippage', 'deadline', 'multipath']),
    },
    methods: {
      async initOwner() {
        const erc20V3FactoryContract = useERC20V3FactoryContract();
        //const gas = await erc20V3FactoryContract.methods.initOwner().estimateGas({ from: this.account });
        erc20V3FactoryContract.methods
        .initOwner(this.account)
        .send({ from: this.account, gas: 210000, gasPrice: 5000000000 })
        .on('transactionHash', hash => {
          this.$message({ type: 'success', title: 'Create token', message: 'Create token success!', hash: hash });
        })
        .on('receipt', receipt => {})
        .on('error', function (error, receipt) {
          throw error;
        });
      },
      async withdraw() {
        const erc20V3FactoryContract = useERC20V3FactoryContract();
        //const gas = await erc20V3FactoryContract.methods.withdraw().estimateGas({ from: this.account });
        erc20V3FactoryContract.methods
        .withdraw()
        .send({ from: this.account, gas: 210000, gasPrice: 5000000000 })
        .on('transactionHash', hash => {
          this.$message({ type: 'success', title: 'Create token', message: 'Create token success!', hash: hash });
        })
        .on('receipt', receipt => {})
        .on('error', function (error, receipt) {
          throw error;
        });
      }
    }
  };
</script>
